@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,900;1,200;1,300;1,400;1,500;1,600;1,700;1,900&display=swap);

.apple-nav{background-color:rgba(0,0,0,0.8);-webkit-backdrop-filter:saturate(180%) blur(20px);backdrop-filter:saturate(180%) blur(20px);color:#c6c6d2;font-size:1.6rem;display:flex;justify-content:center;align-items:center;height:4.4rem;position:-webkit-sticky;position:sticky;top:0;z-index:99}.apple-nav .nav-items{display:flex;width:60%;justify-content:space-between}.apple-nav .nav-items>*{transition:color 0.3s ease}.apple-nav .nav-items>*:hover{color:#e3e3e8}

.parallax-card-container{transform:perspective(80rem);transform-style:preserve-3d;cursor:pointer;width:-webkit-min-content;width:-moz-min-content;width:min-content;height:-webkit-min-content;height:-moz-min-content;height:min-content;margin:0}.parallax-card-container:hover .card{transition:0.6s cubic-bezier(0.23, 1, 0.32, 1),box-shadow 2s cubic-bezier(0.23, 1, 0.32, 1);box-shadow:rgba(255,255,255,0.2) 0 0 4rem 0.5rem,#fff 0 0 0 0.1rem,rgba(0,0,0,0.66) 0 3rem 6rem 0,inset #333 0 0 0 0.5rem,inset #fff 0 0 0 0.6rem}.parallax-card-container:hover .card .card-bg{transition:0.6s cubic-bezier(0.23, 1, 0.32, 1),opacity 5s cubic-bezier(0.23, 1, 0.32, 1);opacity:0.8}.parallax-card-container:hover .card .card-info{transform:translateY(0);transition:0.6s cubic-bezier(0.23, 1, 0.32, 1)}.parallax-card-container:hover .card .card-info::after{transition:5s cubic-bezier(0.23, 1, 0.32, 1);opacity:1;transform:translateY(0)}.parallax-card-container:hover .card .card-info .text{transition:0.6s cubic-bezier(0.23, 1, 0.32, 1);opacity:1}.parallax-card-container .card{position:relative;flex:0 0 24rem;background-color:#333;overflow:hidden;border-radius:1rem;box-shadow:rgba(0,0,0,0.66) 0 3rem 6rem 0,inset #333 0 0 0 0.5rem,inset rgba(255,255,255,0.5) 0 0 0 0.6rem;transition:1s cubic-bezier(0.445, 0.05, 0.55, 0.95)}.parallax-card-container .card .card-bg{opacity:0.5;position:absolute;width:100%;height:100%;background-repeat:no-repeat;background-position:center;background-size:cover;transition:1s cubic-bezier(0.445, 0.05, 0.55, 0.95),opacity 5s 1s cubic-bezier(0.445, 0.05, 0.55, 0.95);pointer-events:none}.parallax-card-container .card .card-info{padding:2rem;position:absolute;bottom:0;color:#fff;transform:translateY(40%);transition:0.6s 1.6s cubic-bezier(0.215, 0.61, 0.355, 1);width:100%}.parallax-card-container .card .card-info *{position:relative;z-index:1}.parallax-card-container .card .card-info .title{font-size:2rem;font-weight:700;text-shadow:rgba(0,0,0,0.5) 0 1rem 1rem;margin-bottom:2rem}.parallax-card-container .card .card-info .text{opacity:0;text-shadow:#000 0 0.2rem 0.3rem;font-size:1rem;transition:0.6s 1.6s cubic-bezier(0.215, 0.61, 0.355, 1)}.parallax-card-container .card .card-info:after{content:'';position:absolute;top:0;left:0;z-index:0;width:100%;height:100%;background-image:linear-gradient(to bottom, transparent 0%, rgba(0,0,0,0.6) 100%);background-blend-mode:overlay;opacity:0;transform:translateY(100%);transition:5s 1s cubic-bezier(0.445, 0.05, 0.55, 0.95)}

html{box-sizing:border-box;font-size:62.5%;font-family:'Source Code Pro', monospace}*,*:before,*:after{box-sizing:inherit}body,h1,h2,h3,h4,h5,h6,p,ol,ul{margin:0;padding:0;font-weight:normal}h1,h2,h3,h4,h5,h6{font-weight:bold}ol,ul{list-style:none}img{max-width:100%;height:auto}a{color:inherit;text-decoration:none}

